* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: #fbf307;
  font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  list-style: none;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100px;
  margin: 50px 0;
}

.form__title {
  font-size: 22px;
  text-align: center;
}

@media all and (min-width: 768px) {
  .form__title {
    font-size: 36px;
  }
}

.form__input {
  height: 30px;
  width: 65%;
  text-align: center;
  margin-top: 10px;
  border: none;
  box-shadow: 3px 4px 25px 0px #6d6868;
}
.pokemon__card {
  width: 200px;
  height: 200px;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 15px;
  box-shadow: 3px 6px 15px 0px #6d6868;
}

.image__container {
  background-color: #DEDEDE;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.pokemon__card-image {
  align-self: center;
}

.pokemon__card-number {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 20px;
  font-size: 12px;
  background-color: #bbbab8;
  color: #9C8975;
}

.pokemon__card-data {
  background-color: #FFF;
  height: 80px;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}

.data__name {
  margin-left: 10px;
  margin-top: 15px;
  text-transform: capitalize;
}

.data__types {
  list-style: none;
  display: flex;
  margin: 5px 10px;
}

.data__types-type {
  border: 1px solid #9C8975;
  border-radius: 6px;
  display: block;
  margin-right: 10px;
  padding: 5px;
  font-size: 12px;
  text-transform: uppercase;
  color: #9C8975;
}


.pokemon__list {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  list-style: none;
}

